/**
 * plus.navigator.checkPermission(permission)
 * @param 	{String}  	CAMERA		//访问摄像头权限
 * @param 	{String}	LOCATION	//定位权限
 * @param 	{String}	CONTACTS	//访问系统联系人权限
 * @param 	{String}	GALLERY		//访问系统相册权限
 * @param 	{String}	NOTIFITION	//消息通知权限
 * @param 	{String}	RECORD		//录音权限
 * @param 	{String}	SHORTCUT	//创建桌面快捷方式
 * 
 * 返回值
 * authorized			//表示程序已被用户授权使用此权限
 * denied				//表示程序已被用户拒绝使用此权限
 * undetermined			//表示程序未确定是否可使用此权限，此时调用对应的API时系统会弹出提示框让用户确认
 * notdeny				//表示程序未被用户拒绝使用此权限（与denied相反，可能是"authorized"或者"undetermined"）
 * unknown				//表示程序未知此权限状态（系统存在权限管理但无法查询）
 * unsupported			//表示程序不支持此权限
 */
// https://ask.dcloud.net.cn/article/35915

import permision from '@/common/js/permission.js';
// permision.judgeIosPermission("location") //判断iOS上是否给予位置权限，有权限返回true，否则返回false
export default {
	data () {
		return {
		}
	},
	methods: {
		/**
		 * 定位权限
		 */
		checkLcation () {
			var pp = plus.navigator.checkPermission('LOCATION');
			switch(pp){
				case 'authorized':
					plus.nativeUI.alert('已开启定位权限');
				break;
				case 'denied':
					plus.nativeUI.alert('已关闭定位权限');
				break;
				case 'undetermined':
					plus.nativeUI.alert('未确定定位权限');
				break;
				case 'unknown':
					plus.nativeUI.alert('无法查询定位权限');
				break;
				default:
					plus.nativeUI.alert('不支持定位权限');
				break;
			}
		},
		
		/**
		 * 摄像头权限
		 */
		checkCamat () {
			var AVCaptureDevice = plus.ios.import("AVCaptureDevice");  
			var authStatus = AVCaptureDevice.authorizationStatusForMediaType('vide');  
			console.log("authStatus:" + authStatus);  
			if (authStatus == 3) { 
				plus.nativeUI.alert('相机权限已经开启');
			} else {  
				plus.nativeUI.alert('相机权限没有开启');
			}  
			plus.ios.deleteObject(AVCaptureDevice);
		},
		
		/**
		 * 推送权限
		 */
		UIApplication () {
			var UIApplication = plus.ios.import("UIApplication");  
			var app = UIApplication.sharedApplication();  
			var enabledTypes = 0;  
			if (app.currentUserNotificationSettings) {  
				var settings = app.currentUserNotificationSettings();  
				enabledTypes = settings.plusGetAttribute("types");  
				console.log("enabledTypes1:" + enabledTypes);  
				if (enabledTypes == 0) {
					plus.nativeUI.alert('推送权限没有开启')
				}else {
					plus.nativeUI.alert('已经开启推送功能')
				}  
				plus.ios.deleteObject(settings);  
			} else {  
				enabledTypes = app.enabledRemoteNotificationTypes();  
				if (enabledTypes == 0) {
					plus.nativeUI.alert('推送权限没有开启')
				} else {  
					plus.nativeUI.alert('已经开启推送功能')
				}  
				console.log("enabledTypes2:" + enabledTypes);  
			}  
			plus.ios.deleteObject(app);  
			plus.ios.deleteObject(UIApplication);
		},
		
		/**
		 * 推送权限
		 */
		avaudiosession () {
			var avaudiosession = plus.ios.import("AVAudioSession");  
			var avaudio = avaudiosession.sharedInstance();  
			var permissionStatus = avaudio.recordPermission();  
			console.log("permissionStatus:" + permissionStatus);  
			if (permissionStatus == 1684369017 || permissionStatus == 1970168948) {
				plus.nativeUI.alert('麦克风权限没有开启')
			} else {  
				plus.nativeUI.alert('麦克风权限已经开启')
			}  
			plus.ios.deleteObject(avaudiosession);
		},
		
		/**
		 * 相册权限
		 */
		PHPhotoLibrary () {
			var PHPhotoLibrary = plus.ios.import("PHPhotoLibrary");  
			var authStatus = PHPhotoLibrary.authorizationStatus();  
			console.log("authStatus:" + authStatus);  
			if (authStatus == 3) {  
				plus.nativeUI.alert('相册权限已经开启')
			} else  {  
				plus.nativeUI.alert('相册权限没有开启')
			}  
			plus.ios.deleteObject(PHPhotoLibrary);
		},
		
		/**
		 * 通讯录权限
		 */
		CNContactStore () {
			// var CNContactStore = plus.ios.import("CNContactStore");  
			// var cnAuthStatus = CNContactStore.authorizationStatusForEntityType(0);  
			// if (cnAuthStatus == 3) {  
			// 	console.log("通讯录权限已经开启");  
			// } else  {  
			// 	console.log("通讯录权限没有开启");  
			// }  
			// plus.ios.deleteObject(CNContactStore);
			var pp = plus.navigator.checkPermission('CONTACTS');
			switch(pp){
				case 'authorized':
					plus.nativeUI.alert('已开启通讯录权限');
				break;
				case 'denied':
					plus.nativeUI.alert('已关闭通讯录权限');
				break;
				case 'undetermined':
					plus.nativeUI.alert('未确定通讯录权限');
				break;
				case 'unknown':
					plus.nativeUI.alert('无法查询通讯录权限');
				break;
				default:
					plus.nativeUI.alert('不支持通讯录权限');
				break;
			}
		},
		
		/**
		 * 日历权限
		 */
		EKEventStore () {
			var EKEventStore = plus.ios.import("EKEventStore");  
			var ekAuthStatus = EKEventStore.authorizationStatusForEntityType(0);  
			if (ekAuthStatus == 3) {  
				plus.nativeUI.alert('日历权限已经开启');
			} else  {  
				plus.nativeUI.alert('日历权限没有开启');
			}  
			plus.ios.deleteObject(EKEventStore);
		},
		
		/**
		 * 备忘录权限
		 */
		ekAuthStatus () {
			var EKEventStore = plus.ios.import("EKEventStore");  
			var ekAuthStatus = EKEventStore.authorizationStatusForEntityType(1);  
			if (ekAuthStatus == 3) {  
				plus.nativeUI.alert('备忘录权限已经开启');
			} else  {  
				plus.nativeUI.alert('备忘录权限没有开启');
			}  
			plus.ios.deleteObject(EKEventStore);
		},
		
		/**
		 * 提示用户开启权限
		 */
		cellularData (param) {
			
			let text = param;
			plus.nativeUI.confirm( "您的" + (param == '' ? '' :  text) +  "权限没有开启，是否去开启？" , function(e) {  
				if (e.index == 0) {  
					var UIApplication = plus.ios.import("UIApplication");  
					var application2 = UIApplication.sharedApplication();  
					var NSURL2 = plus.ios.import("NSURL"); 
					var setting3 = NSURL2.URLWithString("prefs:root=CAMERA");
					var setting2 = NSURL2.URLWithString("app-settings:");  
					application2.openURL(setting2);  

					plus.ios.deleteObject(setting2);  
					plus.ios.deleteObject(setting3);  
					plus.ios.deleteObject(NSURL2);  
					plus.ios.deleteObject(application2);  
				}  
			}, {  
				"buttons": ["Yes", "No"],  
				"verticalAlign": "center"  
			});
		}
	}
}
